.myButton{
  display: block;
  margin: 0 auto;
  border: none;
  padding: 10px 50px;
  cursor: pointer;
  font-weight: 500;
  font-size: 20px;
  transition: all .3s;
  background-color: transparent;
  border: 1px solid rgba(0,0,0,0.5);
  color: rgba(0,0,0,0.5);
  &:hover{
    color: #fff;
    background-color: rgba(0,0,0,0.5);
    transition: all .3s;
  }
}