.myInput{
  width: 100%;
  padding: 10px 15px 3px 45px;
  font-size: 14px;
  font-weight: 300;
  border: none;
  border-bottom: 2px solid rgba(0,0,0,0.5);
  outline: none;
  transition: all .3s;
  margin-bottom: 20px;
  &:focus{
    border-bottom: 2px solid #020202;
    transition: all .3s;
    color: #000;
  }
}

.myInputWrapper{
  border-bottom: 0;
}