.myModal{
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  display: none;
  background-color: rgba(0,0,0, 0.5);
}

.myModalContent{
  padding: 25px;
  background: #fff;
  width: 400px;
}

.myModal.active{
  display: flex;
  justify-content: center;
  align-items: center;
}