@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;500;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul{
  list-style: none;
}

a{
  text-decoration: none;
}

body{
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
}

.fw_300{
  font-weight: 300;
}
.fw_500{
  font-weight: 500;
}
.fw_700{
  font-weight: 700;
}

.text_color{
  color: #020202;
}

.container{
  max-width: 1400px;
  padding: 0 50px;
  margin: 0 auto;
}

header{
  position: fixed;
  left: 0;
  right: 0;
  padding: 15px 0;
  border-bottom: 1px solid #f8f8f8;
  background-color: #f8f8f8;
  ._inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  ._logo img{
    width: 100px;
    height: 50px;
    object-fit: cover;
  }
  ul{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  ul li + li{
    margin-left: 15px;
  }
  ul li a img{
    width: 45px;
    padding: 10px;
    object-fit: cover;
    height: auto;
    transition: all .3s;
  }
  ul li a img:hover{
    transition: all .3s;
    background-color: #e7e7e7;
    border-radius: 10px;
  }
  ul li a span{
    padding-bottom: 2px;
    text-transform: uppercase;
    transition: all .3s;
    background-image: linear-gradient(#000, #000);
    background-position: 0% 100%;
    background-repeat: no-repeat;
    background-size: 0% 2px;
    transition: background-size .3s;
  }
  ul li a span:hover{
    background-size: 100% 2px;
  }
}

.main{
  padding-top: 85px;
  &_inner{
    display: flex;
    align-items: center;
    padding-top: 30px;
  }
  &_sidebar ul li{
    padding: 5px 0 5px 10px;
    margin-bottom: 2px;
    text-transform: uppercase;
    border-left: 1px solid rgba(0,0,0,0.5);
    transition: all .3s;
  }
  &_sidebar ul li.main_sidebar_active{
    border-left: 1px solid #000;
  }
  &_sidebar ul li a{
    color: rgba(0,0,0,0.5);
    font-weight: 700;
    transition: all .3s;
  }
  &_sidebar ul li.main_sidebar_active a{
    color: #000;
  }
  &_sidebar ul li:hover{
    border-left: 1px solid #000;
    transition: all .3s;
  }
  &_sidebar ul li a:hover{
    color: #000;
    transition: all .3s;
  }
}

.modalTitle{
  text-align: center;
  font-size: 28px;
  font-weight: 500;
  margin-bottom: 20px;
}

.list{
  &_h1{
    
  }
  &_wrapper{
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 40px;
  }
}